import React from "react"
import ResearchInterests from "../views/research-interests"
import Classes from "../views/classes"
import People from "../views/people"
import Projects from "../views/projects"
import Publications from "../views/publications"

export default class Information extends React.Component {
  constructor(props) {
    super(props)
    this.handleResearchInterestClick = this.handleResearchInterestClick.bind(this)
    this.handleClassesClick = this.handleClassesClick.bind(this)
    this.handlePeopleClick = this.handlePeopleClick.bind(this)
    this.handleProjectsClick = this.handleProjectsClick.bind(this)
    this.handlePublicationsClick = this.handlePublicationsClick.bind(this)
    this.state = { page: "research-interests" }
  }

  componentDidMount() {
    const params = new URLSearchParams(window.location.search)
    const page = params.get("page")
    if (page === "research-interests") {
      this.setState({ page: "research-interests" })
    } else if (page === "classes") {
      this.setState({ page: "classes" })
    } else if (page === "people") {
      this.setState({ page: "people" })
    } else if (page === "projects") {
      this.setState({ page: "projects" })
    } else if (page === "publications") {
      this.setState({ page: "publications" })
    }
  }

  setPage(page) {
    window.history.pushState(
      "data",
      "Ellen Bass - Drexel Univerity",
      "/?page=" + page
    )
    this.setState({ page: page })
  }

  handleResearchInterestClick() {
    this.setPage("research-interests")
  }

  handleClassesClick() {
    this.setPage("classes")
  }

  handlePeopleClick() {
    this.setPage("people")
  }

  handleProjectsClick() {
    this.setPage("projects")
  }

  handlePublicationsClick() {
    this.setPage("publications")
  }

  render() {
    const page = this.state.page;
    let header;
    let body;

    let researchInterests = <button class="hover:underline mx-2 text-drexel-blue focus:outline-none" onClick={this.handleResearchInterestClick}>Research Interests</button>;
    let classes = <button class="hover:underline mx-2 text-drexel-blue focus:outline-none" onClick={this.handleClassesClick}>Classes</button>;
    let people = <button class="hover:underline mx-2 text-drexel-blue focus:outline-none" onClick={this.handlePeopleClick}>People</button>;
    let projects = <button class="hover:underline mx-2 text-drexel-blue focus:outline-none" onClick={this.handleProjectsClick}>Projects</button>;
    let publications = <button class="hover:underline mx-2 text-drexel-blue focus:outline-none" onClick={this.handlePublicationsClick}>Publications</button>;

    if (page === "research-interests") {
      researchInterests = <button class="border-b-4 border-drexel-yellow mx-2 text-drexel-blue focus:outline-none">Research Interests</button>;
      body = <ResearchInterests></ResearchInterests>
    } else if (page === "classes") {
      classes = <button class="border-b-4 border-drexel-yellow mx-2 text-drexel-blue focus:outline-none">Classes</button>;
      body = <Classes></Classes>
    } else if (page === "people") {
      people = <button class="border-b-4 border-drexel-yellow mx-2 text-drexel-blue focus:outline-none">People</button>;
      body = <People></People>
    } else if (page === "projects") {
      projects = <button class="border-b-4 border-drexel-yellow mx-2 text-drexel-blue focus:outline-none">Projects</button>;
      body = <Projects></Projects>
    } else if (page === "publications") {
      publications = <button class="border-b-4 border-drexel-yellow mx-2 text-drexel-blue focus:outline-none">Publications</button>;
      body = <Publications></Publications>
    }

    header = (
      <div class="flex flex-row justify-center mb-6 max-w-full px-2">
        { researchInterests }
        { classes }
        { people }
        { projects }
        { publications }
      </div>
    )

    return (
      <div class="w-full flex justify-center">
        <div class="py-7">
          {header}
          {body}
        </div>
      </div>
    )
  }
}
